const STORE: Record<string, Record<string, any>> = {};

const mergeStore = (key: string, data: Record<string, any>) => {
  STORE[key] = Object.assign({}, STORE[key], data);
};

const getStore = (key: string): Record<string, any> => {
  return STORE[key] || {};
};

export const setJustCreated = (key: string, isJustCreated: boolean) => {
  mergeStore(key, { isJustCreated });
};
export const isJustCreated = (key: string): boolean => {
  return getStore(key).isJustCreated;
};
